@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f3f3
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */

.sans-pro {
  font-family: 'Source Sans Pro', sans-serif;
}

.sans-pro-caps {
  font-family: 'Source Sans Pro', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
}

.sans-pro-header {
  font-family: 'Source Sans Pro', sans-serif;
  color: gray;
  font-size: 2rem;
}

.permanent-marker-goal {
  font-family: 'Permanent Marker', cursive;
}

.modal-form-header {
  color: gray;
  font-size: 20px;
  float: left;
  margin-right: 10px
}

.strengths-header {
  text-align: center;
  margin: 10px 0px 5px 0px;
  background-color: #ececec;
  padding: 3px;
  font-size: 15px;
  border-radius: 5px;
}

.report-img {
  width: 100%
}

.reports-view {
  background-color: white;
  padding: 20px 20px 0px 20px;
  text-align: center;
}

.reports-view-title {
  background-color: white;
  padding: 0px 20px ;
  text-align: left;
  text-transform: capitalize;
}

.reports-view-hr {
  border-bottom: 1px solid darkgray;
}

.reports-view-heading {
  margin: 0px;
  padding-bottom: 5px;
  font-size: 16px;
  font-weight: bold;
}

.hover-pointer {
  cursor: pointer;
}

.goal-scale {
  color: #4b4b4b;
}

.tab-title-container {
  padding-top: 40px;
  display: inline-block;
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}

.tab-title {
  margin-bottom: 0;
  font-size: 1.5rem;
}

.tab-description {
  margin-top: 20px;
}

.reset-button {
  display: inline-block;
  position: absolute;
  right: 20px;
  bottom: -5px;
}
.reset-button-mobile {
  display: inline-block;
  margin-top: 20px;

}

.adaptive-learning-control {
  text-align: right;
}

/* .adaptive-learning-goal-select {
  width: auto;
  display: inline-block;
} */

.activity-link, .activity-link:hover {
  color: inherit; 
  text-decoration: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cursor {
  -webkit-animation: 1s blink step-end infinite;
          animation: 1s blink step-end infinite
}

@keyframes
  blink {
    from,to {
      color: transparent
    }
    50% {
      color:#000
    }
  }

@-webkit-keyframes 
  blink {
    from,to {
      color:transparent
    }
    50% {
      color:#000}
    }

.unit-folder {
  border-top: 1px solid lightgray;
}
.report-instructions {
  padding: 20px 20px;
  background: white;
}

.comparison-progress-row {
  padding-bottom: 10px;
}

.roster-dropdown {
  box-shadow: none;
  text-align: center;
  padding-bottom: 20px;
  background: white;
}

a:not([href]) {
  color: gray;
}

.navbar-link {
  color: gray;
  font-weight: 300;
  padding: 0 15px;
}

.navbar-link:hover {
  cursor: pointer;
  color: white;
  font-weight: bold;
  text-decoration: none;
  font-weight: 900;
  border-bottom: 2px solid white
}

.clicked {
  color: white;
  font-weight: bold;
  text-decoration: none;
  font-weight: 900;
  border-bottom: 2px solid white
}

.unclicked {
  color: gray;
  font-weight: 300;
  padding: 0 10px;
}

